#main-footer {
  display: flex;
  justify-content: center;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: bold;
  color: #757575;
  margin: 30px;
}

#main-footer a {
  color: #0ec9ff;
}

@media screen and (max-width: 600px) {
  #main-footer {
    text-align: center;
  }
}